/* ChatDope Public (Frontend) CSS */
$light-gray: #f5f5f5;
$gray: #c4c4c4;
$blue: #007bff;
$white: #ffffff;
$green: #28a745;

$font-family: "Open Sans", sans-serif;

.chatdope-container {
	position: fixed;
	bottom: 0;
	right: 20px;
	width: 300px;
	height: 500px;
	font-family: $font-family;
	background-color: $white;
	border: 1px solid $light-gray;
	border-radius: 15px 15px 0 0;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

	&__user-header {
		padding: 8px 15px;
		border-bottom: 1px solid $light-gray;
		background-color: $blue;
		color: $white;
		font-family: $font-family;
		font-weight: bold;
		font-size: 14px;

		.status-dot {
			display: inline-block;
			width: 8px;
			height: 8px;
			border-radius: 50%;
			margin-left: 5px;
			position: relative;
			top: -1px;

			&--online {
				background-color: $green;
			}

			&--offline {
				background-color: $gray;
			}
		}
	}

	&__chats {
		height: calc(100% - 120px);
		overflow-y: auto;
		padding: 10px;
	}

	&__input-box {
		position: absolute;
		bottom: 0;
		width: 100%;
		border-top: 1px solid $light-gray;
		background-color: $white;
		display: flex;
		flex-direction: row;
	}

	&__input {
		width: 80%;
		padding: 15px 0 15px 15px;
		background-color: $white;
		display: flex;

		&-text {
			font-family: $font-family;
			flex: 1;
			padding: 0;
			border: none;
			resize: none;
			outline: none;
			height: 1.5em;
			min-height: 1em;
			max-height: 5em;
		}
	}

	&__submit {
		width: 20%;
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;

		&-button {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 28px;
			height: 28px;
			border-radius: 50%;
			background-color: $blue;
			border: none;
			cursor: pointer;

			svg {
				width: 20px;
				height: 20px;
				fill: $white;
				color: $blue;
			}
		}
	}
}
